.table,
.table:hover {
  font-family: var(--font-noto-sans-kr);
}
.table {
  position: relative;
  font-size: var(--font-size-13xl);
  line-height: 120%;
  font-weight: 100;
  color: var(--color-black);
  text-align: center;
}
.table:hover {
  font-weight: 700;
  cursor: grab;
}
.tableImageIcon {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.furniture {
  position: relative;
  width: 268px;
  height: 268px;
}
.col {
  width: 277.5px;
  height: 270px;
  padding: var(--padding-3xs);
  box-sizing: border-box;
}
.card3,
.col,
.tableCard {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.tableCard {
  align-self: stretch;
  border-radius: var(--br-base);
  background-color: var(--color-whitesmoke-100);
  height: 334px;
  padding: var(--padding-25xl) 0 0;
  box-sizing: border-box;
  gap: var(--gap-21xl);
}
.card3 {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  flex: 1;
}
.card3:hover {
  cursor: grab;
}
@media screen and (max-width: 600px) {
  .card3 {
    flex: unset;
    align-self: stretch;
  }
}
